            <p class="body-1 font-weight-medium"><span class="font-weight-bold">ประเภทองค์กร :</span> {{ sellerUser.nameTypeOfSeller }}</p>
            <p class="body-1 font-weight-medium"><span class="font-weight-bold">ชื่อองค์กร :</span> {{ sellerUser.groupName || '-' }}</p>
