            <p class="body-1 font-weight-medium"><span class="font-weight-bold">ที่อยู่ :</span> บ้านเลขที่ {{ sellerUser.address || '-'}}
              หมู่ {{ sellerUser.moo || '-'}} ตำบล {{ sellerUser.tamName || '-'}} จังหวัด {{ sellerUser.provName || '-'}} {{ sellerUser.postCode || ' '}} </p>
