<script>
import axios from "axios";
import { latLng } from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup, LTooltip } from "vue2-leaflet";

export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
    LTooltip,
  },
  data() {
    return {
      month: [],
      baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
      productMk: [],
      productImgMk: [],
      sellerUser: [],
      area: {},
      areaText: "",
      // Map Data
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      zoom: 6.5,
      center: [0, 0],
      bounds: null,
      showAmount: true,
      showDetails: false,
      selectedItem: 1,
      target: {},
      markers: [
        {
          id: null,
          position: { lat: null, lng: null },
          tooltip: null,
          visible: true,
        },
      ],
      // End Map Data
    };
  },
  methods: {
    // Map
    mapLink() {
      window.open(
        `http://maps.google.com/?q=${this.target.lat},${this.target.long},${this.area.areaName}`,
        "_blank"
      );
    },
    zoomUpdated(zoom) {
      this.zoom = zoom;
    },
    centerUpdated(center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    // End map
    getProductImg(idProductOfMemberMk) {
      let formData = new FormData();
      formData.append("idProductOfMemberMk", idProductOfMemberMk);
      axios
        .post(`${this.baseUrl}/product/getProductImg.php`, formData)
        .then((res) => {
          if (res.data.productImgMk !== 0) {
            this.productImgMk = res.data.productImgMk;
            this.productImgMk.shift();
            console.log("productImgMk", this.productImgMk);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getProductDetail(idProductOfMemberMk) {
      let formData = new FormData();
      formData.append("idProductOfMemberMk", idProductOfMemberMk);
      axios
        .post(`${this.baseUrl}/product/getProductDetail.php`, formData)
        .then((res) => {
          if (res.data.productMk !== 0) {
            this.productMk = res.data.productMk[0];
            this.month = this.productMk.monthOfProduct.split(",");
            console.log("productMk", this.productMk);
          }
        })
        .then(() => {
          this.getSellerInfo();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getSellerInfo() {
      console.log(this.productMk.idSellerMember);
      let formData = new FormData();
      formData.append("idSellerMember", this.productMk.idSellerMember);
      axios
        .post(`${this.baseUrl}/product/postFindSellerMk.php`, formData)
        .then((res) => {
          if (res.data.sellerUser !== 0) {
            this.sellerUser = res.data.sellerUser[0];
            console.log("sellerUser", this.sellerUser);
          }
        })
        .then(() => {
          this.getAllArea();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getAllArea() {
      axios
        .get(`${this.baseUrl}/getAllArea.php`)
        .then((res) => {
          if (res.data.area !== 0) {
            let filter = res.data.area.filter((e, i) => {
              return e.idArea === this.sellerUser.vLinkAreaDetailId_MK;
            });
            this.area = {
              idArea: filter[0].idArea,
              areaName: filter[0].areaName,
              targetName: filter[0].targetName,
            };
            console.log("area", this.area);
          }
        })
        .then((res) => {
          this.getUserLL();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getUserLL() {
      let formData = new FormData();
      formData.append("idArea", this.area.idArea);
      axios
        .post(`${this.baseUrl}/product/getUserLL.php`, formData)
        .then((res) => {
          if (res.data.targetLL !== 0) {
            this.target = res.data.targetLL[0];
            let lat = parseFloat(res.data.targetLL[0].lat);
            let long = parseFloat(res.data.targetLL[0].long);
            console.log("targetLL", this.target);
            this.center[0] = lat;
            this.center[1] = long;
            this.markers[0].id = this.target.idArea;
            this.markers[0].position = {
              lat: lat,
              lng: long,
            };
            this.markers[0].tooltip = this.target.targetName;
            console.log("marker", this.markers[0]);
            this.centerUpdated(this.markers[0].position);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    let queryId = localStorage.idProductOfMemberMk;
    this.getProductDetail(queryId);
    this.getProductImg(queryId);
  },
  created() {},
};
</script>



